/**
 * Application class to display to select an item that the token is
 * associated with
 */
export default class ItemConfigApplication extends Application {
	static get defaultOptions(): ApplicationOptions {
	  const options = super.defaultOptions;
    options.id = "pick-up-stix-item-config";
	  options.template = "modules/pick-up-stix/module/pick-up-stix/templates/item-config.html";
		options.width = 500;
		options.height = 'auto';
		options.minimizable = false;
		options.title = `${game.user.isGM ? 'Configure Loot Container' : 'Loot Container'}`;
		options.resizable = true;
		options.classes = ['dnd5e', 'sheet', 'actor', 'character'];
    return options;
	}

	private _html: any;

	constructor(private _token: Token) {
		super({});
		console.log(`pick-up-stix | select item form | constructed with args:`)
		console.log(this._token);
	}

	activateListeners(html) {
		console.log(`pick-up-stix | SelectItemApplication | activateListeners called with args:`);
		console.log(html);

		super.activateListeners(this._html);
		this._html = html;

		$(html).find(`[data-edit="profile"]`).click(e => this._onEditImage(e));
	}

	getData() {
		return {
			object: this._token,
			profileImagePath: this._token.getFlag('pick-up-stix', 'pick-up-stix.imageContainerOpenPath')
		}
	}

	protected _onEditImage(e) {

	}
}
